/* flex 메인 상단  굿로드 검색 검색버튼*/
.flex-top-bottom__center{
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 5px;
    flex-wrap: wrap;
}
/* flex 메인 상단  우리아이에게 딱 맞는 */
.flex-top-bottom__baseline{
    display: flex;
    justify-content: center;
    align-items: stretch;
    padding: 5px;
    flex-wrap: wrap;
}

