.card-box__white{
    height: 300px;
    background: #FFFFFF;
    box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.04);
    border-radius: 13px;
}
.card-box__content{    
    height: 0px;
    border: 1px solid rgba(87, 51, 0, 0.15);
    margin-left: 30px;
    margin-right: 30px;
}
.card-box__line{    
    height: 0px;
    border: 1px solid rgba(87, 51, 0, 0.15);
    margin-left: 30px;
    margin-right: 30px;
}

/* flex column 아래방향 -- 인기그룹 */
.common-flex--column{
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
}
.common-flex--column .item-basci{
    /* height: 93px; */
    border-radius: 13px;
    background: #FFFFFF;
    box-shadow: 0px -10px 20px rgba(0, 0, 0, 0.04);
    border-radius: 13px;
    margin-bottom: 10px;
}
.common-flex--column .item-title-basci{
    height: 42px;
    /* background: #FFFFFF; */
}

.item-add-basci{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 21px;
    background: #573300;
    border-radius: 5px;
}

/* flex row 오른쪽방향 */
.common-flex--row{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
}
/* flex row 왼쪽방향 */
.common-flex--row__right{
    display: flex;
    flex-direction: row-reverse;
    flex-wrap: wrap;
    align-items: center;
}
/* flex 양쪽 제목 left - right */
.common-flex--row-left-right{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    margin-left: 8px;
    margin-right: 8px;
    height: 44px;
    align-items: center;
}

/* card 284 flex column 아래방향*/
.card-height--284{
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    height: 284px;
    background: #FFFFFF;
    box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.04);
    border-radius: 13px;
}
.card-height--284 .card-blank{
    display: flex;
    flex-direction: column-reverse;
    height: 167px;
    /* background-image: url( "../images/main/group-image1.png" ); */
    background-repeat: no-repeat;
    background-size: cover;
} 
.card-title{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
    background: rgba(255, 255, 255, 0.85);
    padding-left: 24px;
    height: 34px;
} 

.card-content{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
    height: 23px;
} 
