/* search */
.search{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    width: 90%;
    height: 50px;
    background: #FFFFFF;
    box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.04);
    border-radius: 25px;
}
.search-input{
    margin-left: 10px;
}
.search-button{
    margin-right: 10px;
}

/* chip */
.common-chip{
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 7px 17px 7px 17px;
    margin: 0px 10px 10px 0px ;
    background: #FFFFFF;
    border: 1px solid #573300;
    box-sizing: border-box;
    border-radius: 17px;
    font-family: "Elice Digital Baeum",sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 12px;
    text-align: center;
    letter-spacing: 0.03em;
    color: #573300;
}
.common-chip:hover {
    background: #573300;
    color: #FFFFFF;
  }

  .common-chip--height__small{
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 2px 10px 2px 10px;
    margin: 0px 0px 0px 5px ;
    background: #FFFFFF;
    border: 1px solid #573300;
    box-sizing: border-box;
    border-radius: 17px;
    font-family: "Elice Digital Baeum",sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 12px;
    text-align: center;
    letter-spacing: 0.03em;
    color: #573300;
}
.common-chip--height__mini{
    padding: 2px 5px 2px 5px;
    margin: 0px 4px 0px 0px ;
    background: #FFFFFF;
    border: 1px solid #573300;
    box-sizing: border-box;
    border-radius: 17px;
    font-family: "Elice Digital Baeum",sans-serif;
    font-style: normal;
    font-weight: bold;
    font-size: 10px;
    text-align: center;
    letter-spacing: 0.03em;
    color: #573300;
    margin-right: 4px;
}
/* font title bold 21 */
