 /* 모바일 가로, 테블릿 세로 (해상도 ~ 479px)*/ 
 @media all and (max-width:767px) 
{
   .common-wrapper {
       display: grid;
       gap: 0px 0px;
       grid-template-columns: 1fr;
       grid-template-rows: 0px 50px 30px;
       background-image: url( "../images/main/bg-image2.png" ), url( "../images/main/bg-image.png" );
       background-size:1920px 1371px, 1920px 815px; 
       background-position:0px 900px ,0 0;
       background-repeat:no-repeat;
     }
   .common-grid--col3__111 {
       display: grid;
       gap: 20px 20px;
       grid-template-columns: 1fr;
       grid-template-rows: auto;
       /* background-color: rgb(107, 149, 197) ; */
   }
   .common-grid--col3__21 {
       display: grid;
       gap: 0px 0px;
       grid-template-columns: 1fr;
       grid-template-rows: auto;
       /* background-color: rgb(107, 149, 197) ; */
     }  
     .span-good-load{
        font-family: Fredoka One;
        font-style: normal;
        font-weight: normal;
        font-size: 40px;
        line-height: 60px;
        letter-spacing: 0.01em;
        color: #FFB300;
    }  
    .span-group-search{
        font-family: "Sandoll Samliphopangche";
        font-style: normal;
        font-weight: 550;
        font-size: 18px;
        line-height: 22px;
        text-align: center;
        letter-spacing: 0.03em;
        color: #573300;
        padding-top: 10px;
    }  
    .icon-quote{
        width: 16px;
        height: 12px;
    }
}

