
/* PC , 테블릿 가로 (해상도1024px ~)*/ 
@media screen and (min-width: 1024px) 
{
    .common-wrapper{
        display: grid;
        gap: 0px 0px;
        grid-template-columns: auto 1000px auto;
        grid-template-rows: 110px auto auto;
        background-image: url( "../images/main/bg-image2.png" ), url( "../images/main/bg-image.png" );
        background-size:1920px 1371px, 1920px 815px; 
        background-position:0px 900px ,0 0;
        background-repeat:no-repeat;
        /* background-color: rgb(107, 149, 197) ; */
    }
    .common-grid--col3__111 {
        display: grid;
        gap: 0px 0px;
        grid-template-columns: 1fr 1fr 1fr ;
        grid-template-rows: auto;
        /* background-color: rgb(107, 149, 197) ; */
      }
    .common-grid--col3__21 {
        display: grid;
        gap: 0px 0px;
        grid-template-columns: 2fr 1fr ;
        grid-template-rows: auto;
        /* background-color: rgb(107, 149, 197) ; */
      }
      .span-good-load{
        font-family: Fredoka One;
        font-style: normal;
        font-weight: normal;
        font-size: 120px;
        line-height: 145px;
        letter-spacing: 0.01em;
        color: #FFB300;
        padding-top:40px;
    }  
    .span-group-search{
        width: 350px;
        height: 84px;
        font-family: "Sandoll Samliphopangche";
        font-style: normal;
        font-weight: 550;
        font-size: 38px;
        line-height: 42px;  
        text-align: center;
        letter-spacing: 0.03em;
        color: #573300;
        padding-top: 10px;
    }  
    .icon-quote{
        width: 28px;
        height: 24px; 
    }
}